.proyectos {
    max-width: 90%;
    display: grid;
    margin: 2rem auto;
    grid-template-columns: 1fr;
    grid-column-gap: 4rem;
    grid-row-gap: 4rem;
    justify-items: stretch;
    align-items: stretch;
}

.proyectos-element {
    padding: 1rem;
    border-radius: 4px;
    width: 100%;
    display: inline-block;
    text-decoration: none;
    transition: all .3s ease-in;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .31), 0 2px 4px 0 rgba(50, 50, 58, .2);
    &:hover {
        transform: translateY(-.25rem);
        opacity: .9;
    }
}

.proyectos-element-title {
    font-size: 1.5rem;
    font-family: var(--rubik);
    font-weight: bold;
    letter-spacing: 1px;
    display: block;
    margin: 0;
    padding-bottom: .25rem;
}

.proyectos-element-subtitle {
    font-size: .75rem;
    font-family: var(--monospace);
    font-weight: bold;
    text-transform: uppercase;
    display: inline-block;
}

.proyectos-element-text {
    font-size: 1rem;
    font-family: var(--plex);
    margin-top: 0;
    margin-bottom: 0;
    width: 100%;
}

.proyecto-1 {
    background-color: var(--project1bg);
}

.proyecto-1-text {
    color: var(--project1txt);
}

.proyecto-2 {
    background-color: var(--project2bg);
}

.proyecto-2-text {
    color: var(--project2txt);
}

.proyecto-3 {
