}

.proyecto-3-text {
    color: var(--project3txt);
}

.proyecto-4 {
    background-color: var(--project4bg);
}

.proyecto-4-text {
    color: var(--project4txt);
}

.proyecto-5 {
    background-color: var(--project5bg);
}

.proyecto-5-text {
    color: var(--project5txt);
}

.proyecto-6 {
    background-color: var(--project6bg);
}

.proyecto-6-text {
