}

.proyecto-7 {
    background-color: var(--project7bg);
}

.proyecto-7-text {
    color: var(--project7txt);
}

@media only screen and (min-width: 48em) {

    .proyectos {
        grid-template-columns: 1fr 1fr;
        max-width: 80vw;
    }

    .proyectos-element {
        padding: 2rem;
    }

}
